/* tslint:disable jsx-boolean-value */
import React, {Suspense, lazy} from 'react';
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom';
import './App.css';

const baseUrl = process.env.PUBLIC_URL;

const CurrentLocationWeatherHandler = lazy(() => import('./WeatherHandler/CurrentLocationWeatherHandler'));
const CityHandler = lazy(() => import('./CityHandler/CityHandler'));
const WeatherHandler = lazy(() => import('./WeatherHandler/WeatherHandler'));

const App = () => 
  <Router>
    <Suspense fallback={<div>...loading</div>}>
      <div className="App">
        <div className="route-container">
          <Switch>
            <Route exact path={`${baseUrl}/`} component={CurrentLocationWeatherHandler} />
            <Route path={`${baseUrl}/list`} component={CityHandler} />
            <Route path={`${baseUrl}/city/:id`} component={WeatherHandler} />
          </Switch>
        </div>
      </div>
    </Suspense>
  </Router>

export default App;
